import {
  concatStyleSets,
  ITheme,
  getTheme,
  HighContrastSelector,
  keyframes,
  PulsingBeaconAnimationStyles
} from '../../Styling';
import {
  memoizeFunction
} from '../../Utilities';
import { IActivityItemStyles, IActivityItemProps } from './ActivityItem.types';

const DEFAULT_PERSONA_SIZE = '32px';
const COMPACT_PERSONA_SIZE = '16px';
const DEFAULT_ICON_SIZE = '16px';
const COMPACT_ICON_SIZE = '13px';
const ANIMATION_INNER_DIMENSION = '4px';
const ANIMATION_OUTER_DIMENSION = '28px';
const ANIMATION_BORDER_WIDTH = '4px';

export const getStyles = memoizeFunction((
  props: IActivityItemProps,
  theme: ITheme = getTheme(),
  customStyles?: IActivityItemStyles
): IActivityItemStyles => {

  const continuousPulse = PulsingBeaconAnimationStyles.continuousPulseAnimationSingle(
    props.beaconColorOne ? props.beaconColorOne : theme.palette.themePrimary,
    props.beaconColorTwo ? props.beaconColorTwo : theme.palette.themeTertiary,
    ANIMATION_INNER_DIMENSION,
    ANIMATION_OUTER_DIMENSION,
    ANIMATION_BORDER_WIDTH
  );

  const fadeIn: string = keyframes({
    from: { opacity: 0, },
    to: { opacity: 1, }
  });

  const slideIn: string = keyframes({
    from: { transform: 'translateX(-10px)' },
    to: { transform: 'translateX(0)' }
  });

  const continuousPulseAnimation = {
    animationName: continuousPulse,
    animationIterationCount: '1',
    animationDuration: '.8s',
    zIndex: 1
  };

  const slideInAnimation = {
    animationName: slideIn,
    animationIterationCount: '1',
    animationDuration: '.5s',
  };

  const fadeInAnimation = {
    animationName: fadeIn,
    animationIterationCount: '1',
    animationDuration: '.5s',
  };

  const ActivityItemStyles: IActivityItemStyles = {

    root: [
      theme.fonts.small,
      {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'flex-start',
        lineHeight: '17px',
        boxSizing: 'border-box',
        color: theme.palette.neutralSecondary
      },
      (props.isCompact && props.animateBeaconSignal) && fadeInAnimation
    ],

    pulsingBeacon: [
      {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '0px',
        height: '0px',
        borderRadius: '225px',
        borderStyle: 'solid',
        opacity: 0
      },
      (props.isCompact && props.animateBeaconSignal) && continuousPulseAnimation
    ],

    isCompactRoot: {
      alignItems: 'center'
    },

    personaContainer: {
      display: 'flex',
      flexWrap: 'wrap',
      minWidth: DEFAULT_PERSONA_SIZE,
      width: DEFAULT_PERSONA_SIZE,
      height: DEFAULT_PERSONA_SIZE
    },

    isCompactPersonaContainer: {
      display: 'inline-flex',
      flexWrap: 'nowrap',
      flexBasis: 'auto',
      height: COMPACT_PERSONA_SIZE,
      width: 'auto',
      minWidth: '0',
      paddingRight: '6px'
    },

    activityTypeIcon: {
      height: DEFAULT_PERSONA_SIZE,
      fontSize: DEFAULT_ICON_SIZE,
      lineHeight: DEFAULT_ICON_SIZE,
      marginTop: '3px'
    },

    isCompactIcon: {
      height: COMPACT_PERSONA_SIZE,
      minWidth: COMPACT_PERSONA_SIZE,
      fontSize: COMPACT_ICON_SIZE,
      lineHeight: COMPACT_ICON_SIZE,
      color: theme.palette.themePrimary,
      marginTop: '1px',
      position: 'relative',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      selectors: {
        '.ms-Persona-imageArea': {
          margin: '-2px 0 0 -2px',
          border: '2px solid' + theme.palette.white,
          borderRadius: '50%',
          selectors: {
            [HighContrastSelector]: {
              border: 'none',
              margin: '0'
            }
          }
        }
      }
    },

    activityPersona: {
      display: 'block'
    },

    doublePersona: {
      selectors: {
        ':first-child': {
          alignSelf: 'flex-end'
        }
      }
    },

    isCompactPersona: {
      display: 'inline-block',
      width: '8px',
      minWidth: '8px',
      overflow: 'visible'
    },

    activityContent: [
      {
        padding: '0 8px'
      },
      (props.isCompact && props.animateBeaconSignal) && slideInAnimation
    ],

    activityText: {
      display: 'inline'
    },

    isCompactContent: {
      flex: '1',
      padding: '0 4px',
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      overflowX: 'hidden'
    },

    commentText: {
      color: theme.palette.neutralPrimary
    },

    timeStamp: [
      theme.fonts.tiny,
      {
        fontWeight: 400,
        color: theme.palette.neutralSecondary
      }
    ],

    isCompactTimeStamp: {
      display: 'inline-block',
      paddingLeft: '0.3em', // One space character
      fontSize: '1em'
    }
  };

  return concatStyleSets(ActivityItemStyles, customStyles);
});
